import React,{useEffect, useState, useRef} from "react";
import AppRouter from "./Router";
import {authService,dbService }from "../fbase";
import { getAuth, onAuthStateChanged } from "firebase/auth";
import { collection, query, onSnapshot, orderBy } from "firebase/firestore";

function App() {
  const[init, setInit] = useState(false);
  //   ( false) -  isLoggedIn  

  // console.log(authService.currentUser);
  const [userObj,setUserobj] = useState(null);
  const [comments, setComments] = useState([]);
  const [scrollY, setScrollY] = useState(0);
  const [refId, setRefId]= useState(null);
  const tabRef= useRef([]);

  useEffect(()=>{
    const auth = getAuth();
    onAuthStateChanged(auth, (user) => {
      //     ! !
      if (user) {
        setUserobj({
          displayName: user.displayName,
          uid: user.uid,
          updateProfile: (args) => user.updateProfile(args),
          }); //user  

      if (user.displayName === null) { //user isplayName     
        const name = user.email.split("@")[0];
        user.displayName = name;
        }
      } else {
      setUserobj(null);
      }
      setInit(true);
      });

  },[]);

  useEffect(() => { //  
    const q = query(
    collection(dbService, "comments"), //dbService  comments  Docs 
    orderBy("date", "desc"), //  
    );
 
    //      ! ! 
    onSnapshot(q, (snapshot) => { //onSnapshot Snapshot    Snapshot   query  
   
       const commenArr = snapshot.docs.map((document) => ({ //snapshot docs  
    id: document.id,
    ...document.data(),
    }));
    setComments(commenArr); //comments  
    });
    }, []);

  const refreshUser = () => {
    const user = authService.currentUser;
    setUserobj({
      displayName: user.displayName,
      uid: user.uid,
      updateProfile: (args) => user.updateProfile(args),
    });
  };


  return(
  <>
  {init ? 
  <AppRouter 
   refreshUser={refreshUser}
   isLoggedIn={Boolean(userObj)} 
   userObj={userObj} comments={comments} scrollY={scrollY} setScrollY={setScrollY} tabRef={tabRef} refId={refId} setRefId={setRefId}
  /> 
   : "Initializing..." }
  </>)
}
 
export default App;
