import React, {useEffect, useState,useRef} from "react";
import { dbService } from "../fbase";
import {collection,  addDoc } from "firebase/firestore";
import '../css/addcom.css';

function AddPincom({mLoc, userObj, flow, setFlow,imgUrl }) {
  //  ! !    
  
  const [inputs, setInputs] = useState({
    nickname: '',
    commen: ''
  });
  const { nickname,commen } = inputs; //     
  const [cVisible, setCVisible ]= useState(false);
  const inputRef = useRef([]);

    useEffect(()=> { //   
      if(flow === 2) inputRef.current[0].focus(); 
      if(flow === 3) inputRef.current[1].focus(); 
    },[flow]);

    const onChange = (e) => { //    
      const { name, value } = e.target; //   
      setInputs({
        ...inputs, //  input   
        [name]: value // name    value  
      });
    };

    const onNick = (e) => {
      if (nickname === "") {
        return;
      } 
      setCVisible(true);
      setFlow(3);
      };

    const handleOnKeyPress = e => { //   Enter   
        if (e.key === 'Enter') {
          e.preventDefault();
           onNick(); // Enter            
        }
    };
      
    const onSubmit = async(event) => {
        if (commen === "") {
            return;
          }
    event.preventDefault();
    try {
      const pincomObj = { // 
        nickname, 
        commen,
        date: Date.now(),
        creatorId: userObj.uid,
        x: mLoc.x,
        y: mLoc.y,
        imgUrl : imgUrl[mLoc.n-1],
        likeN : 0,
        report : false
     };

     await addDoc(collection(dbService, "comments"), pincomObj); 
     //  pincomObj   document  comments  
    } catch (e) {
    console.error("Error adding document: ", e);
    }
    setInputs({
      nickname: '',
      commen: '',
    });
    setFlow(0);
    };


  return(
    <>
      {!cVisible ?
      <div className="inputForm">
         <input
             className="text_input"
             name="nickname"
             value={nickname}
             onChange={onChange}
             onKeyPress={handleOnKeyPress}
             type="text"
             placeholder=""
             maxLength={10}
            ref={el =>(inputRef.current[0] = el)}
           />
           <button onClick={onNick} ></button>
      </div>  :  
      <form  onSubmit={onSubmit} className="inputForm">
       <input
          className="text_input"
          name="commen"
          value={commen}
          onChange={onChange}
          type="text"
          placeholder=" "
          maxLength={120}
          ref={el =>(inputRef.current[1] = el)}
        />
        <button type="submit"></button>
      </form>}
    </>
  )
};

export default AddPincom;
