import React from "react";
import { BrowserRouter as Router,
    Route,Routes } from "react-router-dom";
import Auth from "./Auth";
import Toon from "./Toon";
import Allcom from "./Allcom";
import UseScroll from "../hooks/UseScroll";

function AppRouter ({isLoggedIn, userObj, comments,scrollY, setScrollY,refId, setRefId, tabRef}) {

    return <Router basename={process.env.PUBLIC_URL} >
        <UseScroll scrollY={scrollY} refId={refId} tabRef={tabRef}/>
    <div>
    <Routes>
        {isLoggedIn ? (
        <>
         <Route exact path="/" element={<Toon userObj={userObj} comments={comments} scrollY={scrollY} setScrollY={setScrollY} refId={refId} tabRef={tabRef} />}  />
         <Route exact path="/allcomment" element={<Allcom comments={comments} setRefId={setRefId}/>} />
        </>) : (
        <Route exact path="/" element={<Auth />} />
        )}

    </Routes>
    </div>
</Router>
} 

export default AppRouter;
