import { useState,useEffect, useRef} from 'react';
import AddPincom from '../components/AddPincom';
import Pincom from '../components/Pincom';
import BottomNavi from "../components/BottomNavi";
import '../css/toon.css';


function Toon({userObj,comments,scrollY, setScrollY, tabRef}) {
  //     
  //   ->   css     
  // 
  //  -      
 
  const [flow,setFlow] = useState(0);

  const[mLoc, setMLoc] = useState({});
  const space = window.innerWidth / 4; 

  const [toonh,setToonh] = useState(0);
  const toonRef = useRef(null);
  const imgUrl=[];
  for(let i = 1; i < 16;i++){ imgUrl[i-1] = `${process.env.PUBLIC_URL}/img/toon${i}.png`};
 
  useEffect(()=> { //flow  
    setToonh(toonRef.current.clientHeight); //toon  
  },[flow])

  //   -     
  const onGrid = (e) => {
    setFlow(1);
  }; 

  const getLoc = (e) => {
    setMLoc({
      x: Math.ceil(e.pageX / space),
      y: Math.ceil(e.pageY / space),
      n: Math.ceil(e.pageY / (toonh / 8))
    }); //  
    setFlow(2);
  };

  return (
    <div className="App">
      <div ref={toonRef} className='toonimg'>
      {imgUrl.map((url,index) =>
      <div key={index}>
        <img  src={url} alt='toon'/>
      </div>   
        )}
      </div>
    

    <div className='pincoms_gridbox'>
    { flow < 1
    && (comments.map((pincom,index) =>
    <Pincom
      ref={el =>(tabRef.current[index] = el)}
      key={pincom.id}
      pincom={pincom} 
      isOwner={pincom.creatorId === userObj.uid}
      mLoc={mLoc}/> ))}
    </div>

      {(flow === 2 || flow === 3) && <AddPincom mLoc={mLoc} userObj={userObj} flow={flow} setFlow={setFlow} imgUrl={imgUrl}/>}  
      {flow === 1 && <div className='grid' onClick={getLoc} toonh={toonh} 
      style={{height:toonh}}> 
      </div> }
      <button onClick={onGrid} style={{ position: 'fixed', bottom: 40, left:'40%'}}> </button>
      <BottomNavi scrollY={scrollY} setScrollY={setScrollY}/>
    </div>
  );
}

export default Toon;
